// Widescreen
@include mediaXl {
  .projects {
    grid-template-columns: repeat(4, 1fr);
  }
}

// Desktops & Laptops
@include mediaLg {
  .projects {
    grid-template-columns: repeat(3, 1fr);
  }
}

// Tablets && Small Laptops
@include mediaMd {
  main {
    align-items: center;
    text-align: center;

    .lg-heading {
      line-height: 1;
      margin-bottom: 1rem;
    }
  }

  ul.menu-nav,
  div.menu-branding {
    float: none;
    width: 100%;
    min-height: 0;

    &.show {
      transform: translate3d(0, 0, 0);
    }
  }

  .menu-nav {
    height: 75vh;
    transform: translate3d(-100%, 0, 0);
    font-size: 24px;
  }

  .menu-branding {
    height: 25vh;
    transform: translate3d(100%, 0, 0);

    .picture {
      width: 150px;
      height: 150px;
    }
  }

  .about-info {
    grid-template-areas:
      'bioimage'
      'bio'
      'box1'
      'box2'
      'box3';

    grid-template-columns: 1fr;
  }

  //Projects
  .experiences {
    grid-template-columns: repeat(2, 1fr);
  }
}

// Smartphones
@include mediaSm {
  main {
    padding: 2rem;
    #home h1 {
      margin-top: 10vh;
    }

    .lg-heading {
      margin-top: 2rem;
      line-height: 4rem;
      font-size: 3rem;
    }
  }

  //About
  .skills,
  .tools,
  .soft-skills {
    list-style: none;
    text-align: center;
    padding: 0;
  }

  //Experiences
  .experiences {
    grid-template-columns: 1fr;
    .experience-item h2 {
      margin-bottom: 1rem;
    }
  }

  .boxes {
    .box {
      max-width: 350px;
      font-size: 16px;
    }
  }
}
