$my-picture: url('../img/Lyon-Setup-min.JPG');

//Menu button
.menu-btn {
  position: absolute;
  z-index: 3;
  right: 35px;
  top: 35px;
  cursor: pointer;
  @include easeOut;

  .btn-line {
    width: 28px;
    height: 3px;
    margin: 0 0 5px 0;
    background: set-text-color($primary-color);
    @include easeOut;
  }

  //Rotate menu button into X
  &.close {
    transform: rotate(180deg);

    .btn-line {
      //Line 1 - Rotate
      &:nth-child(1) {
        transform: rotate(45deg) translate(5px, 5px);
      }

      //Line 2 - Hide
      &:nth-child(2) {
        opacity: 0;
      }

      //Line 3 - Rotate
      &:nth-child(3) {
        transform: rotate(-45deg) translate(7px, -6px);
      }
    }
  }
}

//Menu Overlay
.menu {
  position: fixed;
  top: 0;
  width: 100%;
  // opacity: 0.9;
  visibility: hidden;

  //When we click, we will show links
  &.show {
    visibility: visible;
  }

  //This means .menu-branding, we can nested it with the &
  &-branding,
  &-nav {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    float: left;
    width: 50%;
    height: 100vh;
    overflow: hidden;
  }

  //Nav Side
  &-nav {
    margin: 0;
    padding: 0;
    background: darken($primary-color, 5);
    list-style: none;

    //We translate it to put the nav in the top, then we make a transition to ease it out
    transform: translate3d(0, -100%, 0);
    @include easeOut;

    &.show {
      // Slide nav in from top
      transform: translate3d(0, 0, 0);
    }
  }

  // Branding Side
  &-branding {
    background: $primary-color;
    @include easeOut;
    transform: translate3d(0, 100%, 0);

    // Slide branding in from top
    &.show {
      transform: translate3d(0, 0, 0);
    }

    //Portrait
    .picture {
      width: 350px;
      height: 350px;
      border-radius: 50%;
      border: solid 3px $secondary-color;
    }
  }

  //Nav items
  .nav-item {
    transform: translate3d(600px, 0, 0);
    @include easeOut;

    // Slide items in from right
    &.show {
      transform: translate3d(0, 0, 0);
    }

    &.current > a {
      color: $secondary-color;
    }
  }

  .nav-link {
    display: inline-block;
    position: relative;
    font-size: 30px;
    text-transform: uppercase;
    padding: 1rem 0;
    font-weight: 300;
    color: set-text-color($primary-color);
    text-decoration: none;
    @include easeOut;

    &:hover {
      color: $secondary-color;
    }
  }
}

//Delay each nav item slide by 0.1s
//For loop in SASS, we create an iterator and how many values we want to loop through
@for $x from 1 through 4 {
  //Delaying every item for each one of them come one by one
  .nav-item:nth-child(#{$x}) {
    transition-delay: $x * 0.1s;
  }
}
